.popup__signup-title {
  max-width: 358px;
  margin: 0;
  padding: 0;
  margin-bottom: 26px;
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #000;
}

@media screen and (max-width: 767px) {
  .popup__signup-title {
    font-size: 20px;
    line-height: 24px;
    margin: 0 0 25px;
  }
}
